import {ConnectionHandler, commitMutation, graphql} from 'react-relay'
import type {Environment, RecordSourceSelectorProxy} from 'relay-runtime'

import mutationRequest, {type pinEnvironmentMutation} from './__generated__/pinEnvironmentMutation.graphql'

type PinEnvironmentMutationProps = {
  environment: Environment
  envId: string
  pinned: boolean
  connectionID: string
  // TODO: Add functionality for this handler and spike on if we want a handler for onCompleted
  onError?: (error: Error) => void
  onComplete?: () => void
}

function updateEnvironment(store: RecordSourceSelectorProxy, pinned: boolean, connectionID: string) {
  //Establish connection to the query
  const connectionRecord = store.get(connectionID)
  if (!connectionRecord) {
    return
  }

  // Get the updated information on the environment
  const updatedEnvironment = store.getRootField('pinEnvironment')?.getLinkedRecord('environment')
  if (!updatedEnvironment) {
    return
  }

  // Traverse the edges in the connection
  const edges = connectionRecord.getLinkedRecords('edges')
  if (edges) {
    // Check to see if we already have a pinned environment
    const duplicatedEdge = edges.find(edge => {
      const node = edge.getLinkedRecord('node')
      const environment = node?.getLinkedRecord('environment')
      return environment && environment.getDataID() === updatedEnvironment.getDataID()
    })

    // If the pinned environment does not exist insert it
    if (!duplicatedEdge && pinned) {
      const pinnedEnvironment = store.getRootField('pinEnvironment')?.getLinkedRecord('pinnedEnvironment')
      if (!pinnedEnvironment) {
        return
      }
      const newEdge = ConnectionHandler.createEdge(store, connectionRecord, pinnedEnvironment, 'PinnedEnvironmentEdge')
      ConnectionHandler.insertEdgeAfter(connectionRecord, newEdge)
    }

    // Pinned environment exists so delete it from the connection
    else if (duplicatedEdge && !pinned) {
      const node = duplicatedEdge.getLinkedRecord('node')
      if (!node) {
        return
      }
      const pinnedEnvironmentId = node.getDataID()
      ConnectionHandler.deleteNode(connectionRecord, pinnedEnvironmentId)
      store.delete(pinnedEnvironmentId)
    }
  }
}

graphql`
  mutation pinEnvironmentMutation($input: PinEnvironmentInput!) @raw_response_type {
    pinEnvironment(input: $input) {
      environment {
        id
        isPinned
      }
      pinnedEnvironment {
        id
        databaseId
        position
        environment {
          id
          name
          isPinned
          latestCompletedDeployment {
            databaseId
            createdAt
            latestStatus {
              logUrl
              environmentUrl
              state
            }
            commit {
              messageHeadlineHTML
            }
          }
        }
      }
    }
  }
`

export function pinEnvironment({
  environment,
  envId,
  pinned,
  connectionID,
  onComplete,
  onError,
}: PinEnvironmentMutationProps) {
  return commitMutation<pinEnvironmentMutation>(environment, {
    mutation: mutationRequest,
    variables: {input: {environmentId: envId, pinned}},
    onError,
    onCompleted: onComplete,
    updater: store => updateEnvironment(store, pinned, connectionID),
  })
}
