import {score} from 'fzy.js'
import {FilterValueType, FilterProviderType} from '@github-ui/filter'
import type {
  FilterQuery,
  FilterProvider,
  AnyBlock,
  MutableFilterBlock,
  SuppliedFilterProviderOptions,
  ValueRowProps,
  FilterKey,
  FilterValueData,
  ARIAFilterSuggestion,
  FilterConfig,
  IndexedBlockValueItem,
} from '@github-ui/filter'
import {AsyncFilterProvider} from '@github-ui/filter/providers'
import {ValueIcon, getFilterValue, getUnescapedFilterValue} from '@github-ui/filter/utils'
import {ServerIcon} from '@primer/octicons-react'

export const ENVIRONMENT_FILTER_KEY: FilterKey = {
  displayName: 'Environment',
  key: 'environment',
  priority: 3,
  icon: ServerIcon,
  description: 'Filter by deployment environment',
}

export type Environment = {
  name: string
}

export class EnvironmentFilterProvider extends AsyncFilterProvider<Environment> implements FilterProvider {
  constructor(repositoryScope: string, options?: SuppliedFilterProviderOptions) {
    const ENVIRONMENT_SUGGESTION_ENDPOINT = `/${repositoryScope}/deployments/filter-suggestions/environments`

    super(ENVIRONMENT_FILTER_KEY, options)
    this.suggestionEndpoint = ENVIRONMENT_SUGGESTION_ENDPOINT
    this.validationEndpoint = ENVIRONMENT_SUGGESTION_ENDPOINT

    this.type = FilterProviderType.Select
  }

  async getSuggestions(
    filterQuery: FilterQuery,
    filterBlock: AnyBlock | MutableFilterBlock,
    config: FilterConfig,
    caretIndex?: number | null,
  ) {
    return this.processSuggestions(filterQuery, filterBlock, this.processSuggestion.bind(this), caretIndex)
  }

  protected processSuggestion(environment: Environment, query: string): ARIAFilterSuggestion {
    let priority = ENVIRONMENT_FILTER_KEY.priority
    const envName = environment.name

    if (query && envName) {
      priority -= score(query, envName)
    }

    return {
      type: FilterValueType.Value,
      displayName: envName,
      ariaLabel: envName,
      value: envName,
      priority,
    }
  }

  getValueRowProps(value: FilterValueData): ValueRowProps {
    return {
      text: value.displayName ?? getFilterValue(value.value) ?? '',
      leadingVisual: ValueIcon({providerIcon: this.icon, value}),
    }
  }

  override validateValue(
    filterQuery: FilterQuery,
    value: IndexedBlockValueItem,
    env: Environment | null,
  ): false | Partial<IndexedBlockValueItem> | null | undefined {
    const extractedValue = getUnescapedFilterValue(value.value)
    if (!env || !extractedValue) return false

    return {
      value: extractedValue,
      displayName: env.name,
    }
  }
}
