import {StaticFilterProvider} from '@github-ui/filter/providers'
import {FilterProviderType, type SuppliedFilterProviderOptions} from '@github-ui/filter'
import {CheckCircleIcon} from '@primer/octicons-react'
import {STATUSES} from '../../utils/statuses'

export class StateFilterProvider extends StaticFilterProvider {
  constructor(options?: SuppliedFilterProviderOptions) {
    const filterKeys = {
      displayName: 'State',
      key: 'state',
      priority: 1,
      icon: CheckCircleIcon,
      description: 'Filter by deployment state',
    }
    const opts = {
      ...options,
      filterTypes: {...options?.filterTypes, multiValue: true},
    }
    const filterValues = [
      {
        value: 'success',
        displayName: 'success',
        priority: 1,
        icon: STATUSES.success.icon,
        iconColor: STATUSES.success.color,
      },
      {
        value: 'failure',
        displayName: 'failure',
        priority: 2,
        icon: STATUSES.failure.icon,
        iconColor: STATUSES.failure.color,
      },
      {value: 'error', displayName: 'error', priority: 3, icon: STATUSES.error.icon, iconColor: STATUSES.error.color},

      {
        value: 'inactive',
        displayName: 'inactive',
        priority: 4,
        icon: STATUSES.inactive.icon,
        iconColor: STATUSES.inactive.color,
      },
      {
        value: 'pending',
        displayName: 'pending',
        priority: 4,
        icon: STATUSES.pending.icon,
        iconColor: STATUSES.pending.color,
      },
      {
        value: 'queued',
        displayName: 'queued',
        priority: 6,
        icon: STATUSES.queued.icon,
        iconColor: STATUSES.queued.color,
      },
      {
        value: 'in_progress',
        displayName: 'in progress',
        priority: 7,
        icon: STATUSES.inProgress.icon,
        iconColor: STATUSES.inProgress.color,
      },
      {
        value: 'waiting',
        displayName: 'waiting',
        priority: 8,
        icon: STATUSES.waiting.icon,
        iconColor: STATUSES.waiting.color,
      },
    ]
    super(filterKeys, filterValues, opts)
    this.type = FilterProviderType.Select
  }
}
