import {score} from 'fzy.js'
import {FilterValueType, FilterProviderType} from '@github-ui/filter'
import type {
  FilterQuery,
  FilterProvider,
  AnyBlock,
  MutableFilterBlock,
  SuppliedFilterProviderOptions,
  ValueRowProps,
  FilterKey,
  FilterValueData,
  ARIAFilterSuggestion,
  FilterConfig,
  IndexedBlockValueItem,
} from '@github-ui/filter'
import {AsyncFilterProvider} from '@github-ui/filter/providers'
import {ValueIcon, getFilterValue, getUnescapedFilterValue} from '@github-ui/filter/utils'
import {GitBranchIcon} from '@primer/octicons-react'

export const REF_FILTER_KEY: FilterKey = {
  displayName: 'Ref',
  key: 'ref',
  priority: 4,
  icon: GitBranchIcon,
  description: 'Filter by deployment ref',
}

export type Ref = {
  ref: string
}

export class RefFilterProvider extends AsyncFilterProvider<Ref> implements FilterProvider {
  constructor(repositoryScope: string, options?: SuppliedFilterProviderOptions) {
    const REF_SUGGESTION_ENDPOINT = `/${repositoryScope}/deployments/filter-suggestions/refs`

    super(REF_FILTER_KEY, options)
    this.suggestionEndpoint = REF_SUGGESTION_ENDPOINT
    this.validationEndpoint = REF_SUGGESTION_ENDPOINT

    this.type = FilterProviderType.Select
  }

  async getSuggestions(
    filterQuery: FilterQuery,
    filterBlock: AnyBlock | MutableFilterBlock,
    config: FilterConfig,
    caretIndex?: number | null,
  ) {
    return this.processSuggestions(filterQuery, filterBlock, this.processSuggestion.bind(this), caretIndex)
  }

  protected processSuggestion(ref: Ref, query: string): ARIAFilterSuggestion {
    let priority = REF_FILTER_KEY.priority
    const refName = ref.ref

    if (query && refName) {
      priority -= score(query, refName)
    }

    return {
      type: FilterValueType.Value,
      displayName: refName,
      ariaLabel: refName,
      value: refName,
      priority,
    }
  }

  getValueRowProps(value: FilterValueData): ValueRowProps {
    return {
      text: value.displayName ?? getFilterValue(value.value) ?? '',
      leadingVisual: ValueIcon({providerIcon: this.icon, value}),
    }
  }

  override validateValue(
    filterQuery: FilterQuery,
    value: IndexedBlockValueItem,
    ref: Ref | null,
  ): false | Partial<IndexedBlockValueItem> | null | undefined {
    const extractedValue = getUnescapedFilterValue(value.value)
    if (!ref || !extractedValue) return false

    return {
      value: extractedValue,
      displayName: ref.ref,
    }
  }
}
