import type {PinnedDeploymentData} from '../types/active'
import type {FragmentRef} from 'react-relay'
import {graphql, useRefetchableFragment} from 'react-relay'
import type {SafeHTMLString} from '@github-ui/safe-html'
import type {
  ActivePinnedEnvironments$key,
  ActivePinnedEnvironments$data,
} from './__generated__/ActivePinnedEnvironments.graphql'
import type {
  ActiveStickyDeployments$key,
  ActiveStickyDeployments$data,
} from './__generated__/ActiveStickyDeployments.graphql'
import type {PinnedEnvironmentListRefetch} from './__generated__/PinnedEnvironmentListRefetch.graphql'
import type {StickyDeploymentListRefetch} from './__generated__/StickyDeploymentListRefetch.graphql'
import {DeploymentCardsSection} from './DeploymentCardsSection'
import {useEffect} from 'react'

interface ActiveProps {
  repositoryRef: FragmentRef<ActivePinnedEnvironments$key>
  setConnectionId: (connection: string) => void
  pinnedEnvConnectionId: string
}

type PinnedEnvironmentQueryProps = {
  key: ActivePinnedEnvironments$key
}

type ActiveStickyDeploymentsQueryProps = {
  key: ActiveStickyDeployments$key
}

const PinnedEnvironments = ({key}: PinnedEnvironmentQueryProps) => {
  const [data, refetch] = useRefetchableFragment<PinnedEnvironmentListRefetch, ActivePinnedEnvironments$key>(
    graphql`
      fragment ActivePinnedEnvironments on Repository
      @argumentDefinitions(cursor: {type: "String"}, first: {type: "Int", defaultValue: 10})
      @refetchable(queryName: "PinnedEnvironmentListRefetch") {
        pinnedEnvironments(first: $first, after: $cursor) @connection(key: "Repository_pinnedEnvironments") {
          __id
          edges {
            node {
              __id
              databaseId
              position
              environment {
                id
                name
                isPinned
                latestCompletedDeployment {
                  databaseId
                  createdAt
                  latestStatus {
                    logUrl
                    environmentUrl
                    state
                  }
                  commit {
                    messageHeadlineHTML
                  }
                }
              }
            }
          }
        }
      }
    `,
    key,
  )
  return {data, refetch}
}

const StickyDeployments = ({key}: ActiveStickyDeploymentsQueryProps) => {
  const [data, refetch] = useRefetchableFragment<StickyDeploymentListRefetch, ActiveStickyDeployments$key>(
    graphql`
      fragment ActiveStickyDeployments on Repository
      @argumentDefinitions(
        names: {type: "[String!]", defaultValue: ["github-pages", "production"]}
        first: {type: "Int", defaultValue: 2}
        cursor: {type: "String"}
      )
      @refetchable(queryName: "StickyDeploymentListRefetch") {
        stickyEnvironments: environments(names: $names, first: $first, after: $cursor)
          @connection(key: "Repository_stickyEnvironments") {
          edges {
            node {
              databaseId
              id
              name
              latestCompletedDeployment {
                databaseId
                createdAt
                latestStatus {
                  logUrl
                  environmentUrl
                  state
                }
                commit {
                  messageHeadlineHTML
                }
              }
            }
          }
        }
      }
    `,
    key,
  )
  return {data, refetch}
}

function DeploymentFromPinnedEnvironmentsQuery(
  pinnedData: ActivePinnedEnvironments$data,
): PinnedDeploymentData[] | undefined {
  const environmentNodes = pinnedData?.pinnedEnvironments?.edges
  return environmentNodes
    ?.filter(edge => {
      return edge && edge.node && typeof edge.node.databaseId === 'number'
    })
    ?.filter(edge => {
      const pinnedEnvironment = edge?.node
      const deployment = pinnedEnvironment?.environment?.latestCompletedDeployment
      return typeof deployment?.databaseId === 'number' && pinnedEnvironment?.environment?.isPinned === true
    })
    ?.map(edge => {
      const pinnedEnvironment = edge?.node
      const environment = pinnedEnvironment?.environment
      const deployment = environment?.latestCompletedDeployment
      const deploymentName = deployment?.commit?.messageHeadlineHTML
      const environmentUrl = deployment?.latestStatus?.environmentUrl
      const logUrl = deployment?.latestStatus?.logUrl
      const state = deployment?.latestStatus?.state
      return {
        pinnedPosition: pinnedEnvironment?.position,
        id: deployment?.databaseId,
        envId: environment?.id,
        name: deploymentName ? (deploymentName as SafeHTMLString) : ('No commit message' as SafeHTMLString),
        environment: environment ? environment.name : '',
        environmentURL: typeof environmentUrl === 'string' ? environmentUrl : undefined,
        logURL: typeof logUrl === 'string' ? logUrl : undefined,
        createdAt: deployment ? deployment.createdAt : '',
        state: typeof state === 'string' ? state.toLowerCase() : undefined,
      } satisfies PinnedDeploymentData
    })
    .filter(d => !!d)
}

function DeploymentFromEnvironmentsQuery(
  environmentsData: ActiveStickyDeployments$data,
): PinnedDeploymentData[] | undefined {
  const environmentNodes = environmentsData?.stickyEnvironments?.edges
  return environmentNodes
    ?.filter(edge => {
      return edge && edge.node && typeof edge.node.databaseId === 'number'
    })
    ?.filter(edge => {
      const environment = edge?.node
      const deployment = environment?.latestCompletedDeployment
      return typeof deployment?.databaseId === 'number'
    })
    .map(edge => {
      const environment = edge?.node
      const deployment = environment?.latestCompletedDeployment
      const deploymentName = deployment?.commit?.messageHeadlineHTML
      const environmentUrl = deployment?.latestStatus?.environmentUrl
      const logUrl = deployment?.latestStatus?.logUrl
      const state = deployment?.latestStatus?.state
      return {
        pinnedPosition: -1,
        id: deployment?.databaseId,
        envId: environment?.id,
        name: deploymentName ? (deploymentName as SafeHTMLString) : ('No commit message' as SafeHTMLString),
        environment: environment ? environment.name : '',
        environmentURL: typeof environmentUrl === 'string' ? environmentUrl : undefined,
        logURL: typeof logUrl === 'string' ? logUrl : undefined,
        createdAt: deployment ? deployment.createdAt : '',
        state: typeof state === 'string' ? state.toLowerCase() : undefined,
      } satisfies PinnedDeploymentData
    })
    .filter(d => !!d)
}

export function ActiveDeployments({repositoryRef, setConnectionId, pinnedEnvConnectionId}: ActiveProps) {
  const {data: pinnedData} = PinnedEnvironments({key: repositoryRef})
  const {data: stickyData} = StickyDeployments({key: repositoryRef})

  useEffect(() => {
    if (!pinnedEnvConnectionId && pinnedData.pinnedEnvironments?.__id) {
      setConnectionId(pinnedData.pinnedEnvironments?.__id)
    }
  }, [pinnedData, pinnedEnvConnectionId, setConnectionId])

  const pinnedDeployments = DeploymentFromPinnedEnvironmentsQuery(pinnedData) || []
  // If no pinned environments, check environments named "gh-pages" and "production" for successful deployments
  const stickyDeployments = DeploymentFromEnvironmentsQuery(stickyData) || []
  const deploymentList = pinnedDeployments && pinnedDeployments.length !== 0 ? pinnedDeployments : stickyDeployments

  if (!deploymentList || deploymentList?.length === 0) {
    return null
  }

  deploymentList
    .sort((a, b) => a.environment!.localeCompare(b.environment!) || 0)
    .sort((a, b) => a.pinnedPosition! - b.pinnedPosition!)

  return DeploymentCardsSection(deploymentList)
}

try{ PinnedEnvironments.displayName ||= 'PinnedEnvironments' } catch {}
try{ StickyDeployments.displayName ||= 'StickyDeployments' } catch {}
try{ DeploymentFromPinnedEnvironmentsQuery.displayName ||= 'DeploymentFromPinnedEnvironmentsQuery' } catch {}
try{ DeploymentFromEnvironmentsQuery.displayName ||= 'DeploymentFromEnvironmentsQuery' } catch {}
try{ ActiveDeployments.displayName ||= 'ActiveDeployments' } catch {}