/* eslint eslint-comments/no-use: off */
import {ListView} from '@github-ui/list-view'
import {ListViewMetadata} from '@github-ui/list-view/ListViewMetadata'
import type {SafeHTMLString} from '@github-ui/safe-html'
import {Box, Pagination, Text} from '@primer/react'
import {Suspense, useContext, useEffect, useMemo, useState} from 'react'
import type {FragmentRef} from 'react-relay'
import {graphql, usePaginationFragment} from 'react-relay'
import {EnvironmentContext} from '../App'
import type {DeploymentData} from '../types/list'
import {DeploymentListItem, type Deployment} from './DeploymentListItem'
import {DeploymentListItemFallback} from './DeploymentListItemFallback'
import {FallbackSpinner} from './FallbackSpinner'
import {FilterInput} from './FilterInput'
import {Latest} from './Latest'
import {MissingDeployments} from './MissingDeployments'
import type {DeploymentsPaginatedQuery} from './__generated__/DeploymentsPaginatedQuery.graphql'
import type {DeploymentsTablePagination$key} from './__generated__/DeploymentsTablePagination.graphql'

const PAGE_SIZE = 25

type DeploymentTableProps = {
  repositoryRef: FragmentRef<DeploymentsTablePagination$key>
  repoScope: string
}

type PaginationQueryProps = {
  key: DeploymentsTablePagination$key
}

function PaginateDeployments({key}: PaginationQueryProps) {
  const {data, loadNext, hasNext, isLoadingNext, refetch} = usePaginationFragment<
    DeploymentsPaginatedQuery,
    DeploymentsTablePagination$key
  >(
    graphql`
      fragment DeploymentsTablePagination on Repository
      @argumentDefinitions(
        cursor: {type: "String"}
        first: {type: "Int", defaultValue: 25}
        env: {type: "[String!]", defaultValue: []}
        filterBy: {type: "DeploymentFilters", defaultValue: null}
      )
      @refetchable(queryName: "DeploymentsPaginatedQuery") {
        deployments(
          first: $first
          after: $cursor
          environments: $env
          orderBy: {field: CREATED_AT, direction: DESC}
          filterBy: $filterBy
        ) @connection(key: "Repository_deployments") {
          edges {
            node {
              __id
              databaseId
              commitOid
              createdAt
              environment
              latestStatus {
                logUrl
                environmentUrl
                state
              }
              refName
              repository {
                name
                owner {
                  login
                }
              }
              creator {
                login
                avatarUrl
                url
                ... on Bot {
                  id
                  login
                }
              }
              checkRun {
                checkSuite {
                  workflowRun {
                    url
                    runNumber
                    workflow {
                      name
                    }
                  }
                }
              }
              commit {
                messageHeadlineHTML
              }
            }
          }
          pageInfo {
            endCursor
            startCursor
            hasPreviousPage
            hasNextPage
          }
          totalCount
        }
      }
    `,
    key,
  )
  return {data, loadNext, hasNext, isLoadingNext, refetch}
}

/**
 * See ListView stories for a representation of this component.
 * ui/packages/list-view/src/stories/Deployments.stories.tsx
 * https://ui.githubapp.com/storybook/?path=/story/recipes-list-view-dotcom-pages--deployments
 */
export function DeploymentsTable({repositoryRef, repoScope}: DeploymentTableProps) {
  const {
    data: deploymentsData,
    loadNext: loadNextDeployments,
    isLoadingNext: isLoadingNextDeployments,
    hasNext: hasNextDeployments,
    refetch: refetchDeployments,
  } = PaginateDeployments({key: repositoryRef})
  const [currentPage, setCurrentPage] = useState(1)
  const selectedEnv = useContext(EnvironmentContext)

  useMemo(() => {
    refetchDeployments({filterBy: selectedEnv == null ? null : {environments: [selectedEnv]}})
    setCurrentPage(1)
  }, [refetchDeployments, selectedEnv])

  const deploymentDataGraph = useMemo<Deployment[] | undefined>(
    () =>
      deploymentsData.deployments?.edges
        ?.filter(edge => edge && edge.node && typeof edge.node.databaseId === 'number')
        ?.map(edge => {
          const deployment = edge!.node!
          const deploymentName = deployment.commit?.messageHeadlineHTML
          const environmentUrl = deployment.latestStatus?.environmentUrl
          const logUrl = deployment.latestStatus?.logUrl
          const environment = deployment.environment
          return {
            name: deploymentName ? (deploymentName as SafeHTMLString) : ('No commit message' as SafeHTMLString),
            id: deployment.databaseId!,
            node: deployment.__id,
            gitRef: deployment.refName || undefined,
            environment: typeof environment === 'string' ? environment : undefined,
            environmentURL: typeof environmentUrl === 'string' ? environmentUrl : undefined,
            creatorName: deployment.creator.login,
            creatorAvatar: deployment.creator.avatarUrl,
            creatorPath: deployment.creator.url,
            state: deployment.latestStatus?.state.toLowerCase() as DeploymentData['state'],
            logURL: typeof logUrl === 'string' ? logUrl : undefined,
            owner: deployment.repository.owner.login,
            repo: deployment.repository.name,
            createdAt: deployment.createdAt,
            workflowFilePath: deployment.checkRun?.checkSuite?.workflowRun?.url,
            workflowRunNumber: deployment.checkRun?.checkSuite?.workflowRun?.runNumber,
            workflowName: deployment.checkRun?.checkSuite?.workflowRun?.workflow?.name,
            sha: deployment.commitOid,
            trigger: 'via',
          } satisfies Deployment
        })
        .filter(d => !!d),
    [deploymentsData],
  )

  const dataCount = deploymentsData.deployments.totalCount
  const [currentData, setCurrentData] = useState(deploymentDataGraph! as DeploymentData[])

  const latest = deploymentDataGraph as DeploymentData[]

  let latestId = -1
  // sort the latest deployments by id and get the latest one by createdAt
  if (latest.length !== 0) {
    latestId = latest.reduce((newest, deployment) => {
      return deployment.createdAt > newest.createdAt ? deployment : newest
    }, latest[0]!).id
  }

  useEffect(() => {
    const start = (currentPage - 1) * PAGE_SIZE
    const end = start + PAGE_SIZE
    setCurrentData(deploymentDataGraph?.slice(start, end) as DeploymentData[])
  }, [currentPage, deploymentDataGraph])

  const onPageChange = (evt: {preventDefault: () => void}, p: number) => {
    evt.preventDefault()
    if (hasNextDeployments && !isLoadingNextDeployments) {
      loadNextDeployments(PAGE_SIZE)
    }
    setCurrentPage(p)
  }

  const title = `${dataCount.toLocaleString()} deployments`

  return (
    <div>
      <Suspense fallback={<FallbackSpinner />}>{selectedEnv && <Latest latest={latest} />}</Suspense>
      <Box sx={{mb: 3}}>
        <FilterInput setCurrentPage={setCurrentPage} repoScope={repoScope} refetch={refetchDeployments} />
      </Box>

      <Suspense fallback={<FallbackSpinner />}>
        {deploymentsData.deployments.totalCount !== 0 ? (
          <div data-testid="deployments-table">
            <Box sx={{borderWidth: 1, borderStyle: 'solid', borderRadius: 2, borderColor: 'border.default'}}>
              <ListView
                metadata={<ListViewMetadata title={<Text sx={{fontWeight: 600, fontSize: 1}}>{title}</Text>} />}
                title={title}
                titleHeaderTag="h3"
              >
                {isLoadingNextDeployments ? (
                  <FallbackSpinner />
                ) : (
                  currentData.map((deployment, index) => (
                    <Suspense
                      fallback={
                        <>
                          <DeploymentListItemFallback key={index} latestId={latestId} {...deployment} />
                        </>
                      }
                      key={index}
                    >
                      <DeploymentListItem key={index} latestId={latestId} {...deployment} />
                    </Suspense>
                  ))
                )}
              </ListView>
            </Box>
            <div>
              <Pagination
                pageCount={Math.ceil(dataCount / PAGE_SIZE)}
                currentPage={currentPage}
                onPageChange={onPageChange}
                showPages={false}
              />
            </div>
          </div>
        ) : (
          <MissingDeployments />
        )}
      </Suspense>
    </div>
  )
}

try{ PaginateDeployments.displayName ||= 'PaginateDeployments' } catch {}
try{ DeploymentsTable.displayName ||= 'DeploymentsTable' } catch {}