import {Box, Text, Link} from '@primer/react'
import {GitHubAvatar} from '@github-ui/github-avatar'
import type {DeploymentData} from '../../types/list'
import {userHovercardPath} from '@github-ui/paths'

export const Trigger = ({
  creatorPath,
  creatorName,
  creatorAvatar,
  workflowRunNumber,
  workflowName,
}: Pick<DeploymentData, 'creatorPath' | 'creatorName' | 'creatorAvatar' | 'workflowRunNumber' | 'workflowName'>) => {
  return (
    <Box as="span" sx={{display: 'inline'}}>
      <Link
        href={creatorPath}
        muted
        data-hovercard-type="user"
        data-hovercard-url={userHovercardPath({owner: creatorName})}
        sx={{verticalAlign: 'middle'}}
        target="_blank"
      >
        <GitHubAvatar src={creatorAvatar} size={16} sx={{mb: 1, mr: 1}} />
        {creatorName}
      </Link>

      {workflowRunNumber && workflowName && (
        <Text as="span" sx={{ml: 1}}>
          {`via ${workflowName} #${workflowRunNumber}`}
        </Text>
      )}
    </Box>
  )
}

try{ Trigger.displayName ||= 'Trigger' } catch {}