import {useContext, useCallback, useState, useMemo, useTransition} from 'react'
import {EnvironmentContext} from '../App'
import {useAppPayload} from '@github-ui/react-core/use-app-payload'
import type {RefetchFnDynamic} from 'react-relay'
import {
  Filter,
  type FilterQuery,
  type FilterProvider,
  BlockType,
  SubmitEvent,
  ValidationMessage,
} from '@github-ui/filter'
import {EnvironmentFilterProvider} from '../providers/filters/environment'
import {RefFilterProvider} from '../providers/filters/ref'
import {CreatorFilterProvider} from '../providers/filters/creator'
import {StateFilterProvider} from '../providers/filters/state'
import type {DeploymentsPaginatedQuery} from './__generated__/DeploymentsPaginatedQuery.graphql'
import type {DeploymentsTablePagination$key} from './__generated__/DeploymentsTablePagination.graphql'
import {getFilterValue} from '@github-ui/filter/utils'
import type {AppPayload} from '../types/app-payload'

type FilterInputProps = {
  repoScope: string
  setCurrentPage: (value: number) => void
  refetch: RefetchFnDynamic<DeploymentsPaginatedQuery, DeploymentsTablePagination$key>
}

export const FilterInput = ({repoScope, refetch: refetchDeployments, setCurrentPage}: FilterInputProps) => {
  const [validationMessage, setValidationMessage] = useState<string[]>([])
  const validationCallback = useCallback((_: string[], request: FilterQuery) => {
    const rawText = []
    for (const block of request.blocks) {
      if (block.type === BlockType.Text) {
        const errorMessage = `Text will be ignored since log searching is not yet available: <pre>${block.raw}</pre>`
        rawText.push(errorMessage)
      }
    }
    if (rawText.length > 0 || request.errors.length > 0) {
      setValidationMessage([...request.errors, ...rawText])
    } else {
      setValidationMessage([])
    }
  }, [])
  const selectedEnv = useContext(EnvironmentContext)
  const [, startTransition] = useTransition()
  const {currentUser} = useAppPayload<AppPayload>()

  const providers = useMemo<FilterProvider[]>(() => {
    const filterProviders: FilterProvider[] = [
      new RefFilterProvider(repoScope, {
        filterTypes: {
          // "assignee:monalisa" -- The basics!
          inclusive: true,
          // "-assignee:monalisa" -- Adds `-` negation keyword modifier
          exclusive: false,
          // "assignee:monalisa assignee:hubot" -- Adds `AND` operators
          multiKey: false,
          // "assignee:monalisa,hubot" -- Adds `OR` operators
          multiValue: true,
          // "assignee:none" -- Adds the literal value `none` for when no value is present
          valueless: false,
        },
      }),
      new CreatorFilterProvider(
        {
          showAtMe: true,
          currentUserLogin: currentUser.login,
          currentUserAvatarUrl: currentUser.avatarUrl,
          repositoryScope: repoScope,
        },
        {
          filterTypes: {
            // "assignee:monalisa" -- The basics!
            inclusive: true,
            // "-assignee:monalisa" -- Adds `-` negation keyword modifier
            exclusive: false,
            // "assignee:monalisa assignee:hubot" -- Adds `AND` operators
            multiKey: false,
            // "assignee:monalisa,hubot" -- Adds `OR` operators
            multiValue: true,
            // "assignee:none" -- Adds the literal value `none` for when no value is present
            valueless: false,
          },
        },
      ),
      new StateFilterProvider({
        filterTypes: {
          // "assignee:monalisa" -- The basics!
          inclusive: true,
          // "-assignee:monalisa" -- Adds `-` negation keyword modifier
          exclusive: false,
          // "assignee:monalisa assignee:hubot" -- Adds `AND` operators
          multiKey: false,
          // "assignee:monalisa,hubot" -- Adds `OR` operators
          multiValue: true,
          // "assignee:none" -- Adds the literal value `none` for when no value is present
          valueless: false,
        },
      }),
    ]

    // Exclude adding the EnvironmentFilterProvider on any of the environment-specific views
    if (!selectedEnv) {
      filterProviders.push(
        new EnvironmentFilterProvider(repoScope, {
          filterTypes: {
            // "assignee:monalisa" -- The basics!
            inclusive: true,
            // "-assignee:monalisa" -- Adds `-` negation keyword modifier
            exclusive: false,
            // "assignee:monalisa assignee:hubot" -- Adds `AND` operators
            multiKey: false,
            // "assignee:monalisa,hubot" -- Adds `OR` operators
            multiValue: true,
            // "assignee:none" -- Adds the literal value `none` for when no value is present
            valueless: false,
          },
        }),
      )
    }

    return filterProviders
  }, [repoScope, selectedEnv, currentUser])

  const modifyFilterObj = useCallback(
    (
      filterObj: Record<string, string[] | string | undefined>,
      provider: FilterProvider,
      key: string,
      value: string[],
    ): void => {
      let targetValue: string[] | string | undefined = value

      if (provider instanceof StateFilterProvider) {
        targetValue = targetValue.map(v => v.toUpperCase())
      }

      if (provider instanceof CreatorFilterProvider) {
        targetValue = targetValue.map(v => (v === '@me' ? currentUser.login : v))
      }

      if (provider?.options?.filterTypes?.multiValue === true) {
        // Hacky pluralize for now because it works for our current keys
        key = `${key}s`
      } else {
        targetValue = value[0]
      }

      filterObj[key] = targetValue
    },
    [currentUser],
  )

  const onFilterSubmit = useCallback(
    (request: FilterQuery, eventType: SubmitEvent) => {
      startTransition(() => {
        const filterObj: Record<string, string[] | string | undefined> = {}

        // Don't proceed if the event type isn't an explicit act of submitting
        if (![SubmitEvent.ExplicitSubmit, SubmitEvent.DialogSubmit].includes(eventType)) {
          return
        }

        // Don't proceed if anything is invalid at the top-level query syntax (i.e. not well-formed)
        if (request.errors.length > 0) {
          return
        }

        for (const block of request.blocks) {
          // Ignores blocks that aren't filters for now
          if (block.type !== BlockType.Filter) {
            continue
          }

          const filterBlock = block
          // If the Filter component deems a block invalid, bail out so we don't waste query resources
          const isValid = filterBlock?.valid === true
          if (!isValid) {
            return
          }

          const provider = filterBlock.provider
          const key = filterBlock.key?.value
          const values = (filterBlock.value?.values || []).map(v => getFilterValue(v.value) ?? '').filter(Boolean)

          modifyFilterObj(filterObj, provider, key, values)
        }

        // Forcibly include the currently selected environment if needed
        if (selectedEnv) {
          filterObj['environments'] = [selectedEnv]
        }

        // Refetch!
        refetchDeployments({filterBy: filterObj})
        setCurrentPage(1)
      })
    },
    [selectedEnv, refetchDeployments, modifyFilterObj, setCurrentPage],
  )

  return (
    <div>
      <Filter
        id="deployments-filter"
        data-testid="deployments-filter-input"
        context={{repo: repoScope}}
        filterButtonVariant="normal"
        variant="full"
        label="Filter deployments"
        placeholder="Filter deployments"
        providers={providers}
        initialFilterValue={''}
        onSubmit={onFilterSubmit}
        onValidation={validationCallback}
        showValidationMessage={false}
      />
      {validationMessage.length > 0 && (
        <ValidationMessage
          data-testid="filter-warning-deployments"
          messages={validationMessage}
          id={'deployments-validation-message'}
        />
      )}
    </div>
  )
}

try{ FilterInput.displayName ||= 'FilterInput' } catch {}