import {Box, Heading} from '@primer/react'
import type {DeploymentData} from '../types/list'
import {DeploymentCardsSection} from './DeploymentCardsSection'
import {useContext} from 'react'
import {EnvironmentContext} from '../App'

const latestStates = ['active', 'success', 'failure', 'error']

interface LatestProps {
  latest: DeploymentData[]
}

export function Latest({latest}: LatestProps) {
  const selectedEnv = useContext(EnvironmentContext)

  const deploymentListForEnv = latest
    .filter(deployment => {
      return latestStates.includes(deployment.state?.toLowerCase())
    })
    .sort((a, b) => {
      // createdAt is in iso8601 format, which is sortable as a string
      if (b.createdAt < a.createdAt) return -1
      if (b.createdAt > a.createdAt) return 1
      return 0
    })
    .filter((deployment, index, self) => {
      return (
        self.findIndex(
          t => t.environment === deployment.environment && t.workflowFilePath === deployment.workflowFilePath,
        ) === index
      )
    })

  const pageTitle = selectedEnv === null ? 'All deployments' : `${selectedEnv} deployments`

  if (deploymentListForEnv.length === 0) {
    return (
      <Box sx={{display: 'flex', flexDirection: 'column', paddingY: 2}}>
        <Heading as="h2" sx={{fontWeight: 'normal', fontSize: 22}}>
          {pageTitle}
        </Heading>
      </Box>
    )
  }

  return DeploymentCardsSection(deploymentListForEnv)
}

try{ Latest.displayName ||= 'Latest' } catch {}