import {App} from './App'
import {registerReactAppFactory} from '@github-ui/react-core/register-app'
import {FallbackSpinner} from './components/FallbackSpinner'
import {createElement} from 'react'
import {Deployments} from './pages/Deployments'
import {relayRoute} from '@github-ui/relay-route'
import {relayEnvironmentWithMissingFieldHandlerForNode} from '@github-ui/relay-environment'
import DEPLOYMENTS_PAGE_QUERY from './pages/__generated__/DeploymentsPageQuery.graphql'

const relayEnvironment = relayEnvironmentWithMissingFieldHandlerForNode()

registerReactAppFactory('repo-deployments', () => ({
  App,
  routes: [
    relayRoute({
      path: '/:owner/:repo/deployments',
      resourceName: 'Deployments',
      componentLoader: async () => {
        const module = await import('./pages/Deployments')
        return module.Deployments
      },
      Component: Deployments,
      queryConfigs: {
        deploymentQuery: {
          concreteRequest: DEPLOYMENTS_PAGE_QUERY,
          variableMappers: routeParams => {
            return {
              owner: routeParams.pathParams['owner'],
              repo: routeParams.pathParams['repo'],
            }
          },
        },
      },
      title: '',
      relayEnvironment,
      fallback: createElement(FallbackSpinner),
    }),
    relayRoute({
      path: '/:owner/:repo/deployments/:environment',
      resourceName: 'Deployments',
      componentLoader: async () => {
        const module = await import('./pages/Deployments')
        return module.Deployments
      },
      Component: Deployments,
      queryConfigs: {
        deploymentQuery: {
          concreteRequest: DEPLOYMENTS_PAGE_QUERY,
          variableMappers: routeParams => {
            return {
              owner: routeParams.pathParams['owner'],
              repo: routeParams.pathParams['repo'],
            }
          },
        },
      },
      title: '',
      relayEnvironment,
      fallback: createElement(FallbackSpinner),
    }),
  ],
}))
