import {SafeHTMLText, type SafeHTMLString} from '@github-ui/safe-html'
import {LinkExternalIcon} from '@primer/octicons-react'
import {Octicon, Box, Heading, Text, Link, RelativeTime} from '@primer/react'
import {STATUSES} from '../utils/statuses'
import {useContext} from 'react'
import {EnvironmentContext} from '../App'

const maxDisplay = 10

export type DeploymentCardProps = {
  name: SafeHTMLString | undefined
  environment: string | undefined
  createdAt: string | undefined
  logURL: string | undefined
  environmentURL: string | undefined
  state: string | undefined
}

function StatusBadge(state: string | undefined) {
  if (state && state in STATUSES) {
    const {color, icon} = STATUSES[state as keyof typeof STATUSES]
    return <Octicon aria-label={`Deployment ${state}`} icon={icon} sx={{color}} />
  } else {
    return null
  }
}

export function DeploymentCardsSection(data: DeploymentCardProps[]) {
  const selectedEnv = useContext(EnvironmentContext)
  const environmentAdjective = 'pinned'
  const header = !selectedEnv ? 'All deployments' : `${selectedEnv} deployments`
  return (
    <div>
      <Box sx={{display: 'flex', flexDirection: 'column'}}>
        <Heading as="h2" sx={{fontWeight: 'normal', fontSize: 22, mb: 3}}>
          {header}
        </Heading>
        <Text data-testid={`${environmentAdjective}-header`} sx={{color: 'fg.subtle', pr: 1}}>
          {selectedEnv ? 'Latest deployments' : `Latest deployments from ${environmentAdjective} environments`}
        </Text>
      </Box>
      <Box sx={{display: 'grid', gridTemplateColumns: [null, '1fr 1fr'], gap: 2, mt: 2, mb: 4}}>
        {data.slice(0, maxDisplay).map((deployment, index) => (
          <Box
            data-testid="deployment-card"
            sx={{
              borderWidth: 1,
              borderStyle: 'solid',
              borderRadius: 2,
              borderColor: 'border.default',
              p: 2,
              display: 'flex',
              justifyContent: 'space-between',
            }}
            key={index}
          >
            <Box sx={{p: 2}}>
              <div>
                {StatusBadge(deployment.state)}
                <Text sx={{fontWeight: 'bold', pl: 1}}>{deployment.environment}</Text>
              </div>
              <div>
                <Text sx={{fontSize: 0, color: 'fg.muted', pl: 1, pr: 1}}>Last</Text>
                {deployment.logURL ? (
                  <Link href={deployment.logURL} sx={{fontSize: 0}} muted underline>
                    deployed
                  </Link>
                ) : (
                  <Text sx={{fontSize: 0, color: 'fg.muted'}}>deployed</Text>
                )}
                <RelativeTime
                  sx={{fontSize: 0, color: 'fg.muted', pl: 1}}
                  datetime={deployment.createdAt}
                  format="relative"
                />
              </div>
              {deployment.environmentURL ? (
                <div>
                  <Link href={deployment.environmentURL} sx={{pl: 1}}>
                    {deployment.environmentURL}
                  </Link>
                </div>
              ) : (
                <div>
                  <Text sx={{pl: 1, fontSize: 0}}>
                    <SafeHTMLText as="span" html={deployment.name!} />
                  </Text>
                </div>
              )}
            </Box>
            <Box as="span" sx={{p: 2}}>
              {deployment.environmentURL && (
                <a href={deployment.environmentURL} aria-label={`View deployment to ${deployment.environment}`}>
                  <Octicon
                    icon={LinkExternalIcon}
                    sx={{
                      color: 'fg.subtle',
                    }}
                  />
                </a>
              )}
            </Box>
          </Box>
        ))}
      </Box>
    </div>
  )
}

try{ StatusBadge.displayName ||= 'StatusBadge' } catch {}
try{ DeploymentCardsSection.displayName ||= 'DeploymentCardsSection' } catch {}