/* eslint eslint-comments/no-use: off */
import type {DeploymentData} from '../types/list'
import {RelativeTime, Octicon} from '@primer/react'
import {LinkExternalIcon} from '@primer/octicons-react'
import {testIdProps} from '@github-ui/test-id-props'
import {GitRef} from './list-components/GitRef'
import {ListItemMetadata} from '@github-ui/list-view/ListItemMetadata'

type DeploymentListItemMetadataProps = Pick<
  DeploymentData,
  'owner' | 'gitRef' | 'refExists' | 'repo' | 'sha' | 'pullRequestID' | 'pullRequestPath' | 'prefix' | 'environmentURL'
> & {
  createdAt?: DeploymentData['createdAt']
}

export const DeploymentListItemMetadata = ({
  owner,
  gitRef,
  refExists,
  repo,
  sha,
  pullRequestID,
  pullRequestPath,
  prefix,
  createdAt,
  environmentURL,
}: DeploymentListItemMetadataProps) => (
  <>
    {gitRef && (
      <ListItemMetadata variant="primary" sx={{pr: 6, overflow: 'hidden'}}>
        <GitRef
          owner={owner}
          repo={repo}
          gitRef={gitRef}
          refExists={refExists}
          sha={sha}
          pullRequestID={pullRequestID}
          pullRequestPath={pullRequestPath}
          prefix={prefix}
        />
      </ListItemMetadata>
    )}
    <ListItemMetadata alignment="right" sx={{width: '8em'}}>
      <RelativeTime datetime={createdAt} format="relative" prefix="" />
    </ListItemMetadata>
    <ListItemMetadata alignment="right" sx={{width: '20px'}} aria-label={environmentURL && 'Environment URL'}>
      {environmentURL && (
        <a href={environmentURL} {...testIdProps('deployments-list-environment-url')}>
          <Octicon icon={LinkExternalIcon} sx={{color: 'fg.subtle'}} />
        </a>
      )}
    </ListItemMetadata>
  </>
)

try{ DeploymentListItemMetadata.displayName ||= 'DeploymentListItemMetadata' } catch {}