import {Box, SplitPageLayout} from '@primer/react'
import {graphql, usePreloadedQuery, type EntryPointComponent} from 'react-relay'
import {DeploymentsTable} from '../components/DeploymentsTable'
import {ActiveDeployments} from '../components/Active'
import type {DeploymentsPageQuery} from './__generated__/DeploymentsPageQuery.graphql'
import {EnvironmentList} from '../components/EnvironmentList'
import {EnvironmentContext} from '../App'
import {Suspense, useContext, useState} from 'react'
import {FallbackSpinner} from '../components/FallbackSpinner'

export const CurrentDeployments = graphql`
  query DeploymentsPageQuery($owner: String!, $repo: String!) {
    repository(owner: $owner, name: $repo) {
      name
      viewerCanAdminister
      canUseEnvironments
      owner {
        login
      }
      ...EnvironmentListListings
      ...EnvironmentListingsPinned
      ...DeploymentsTablePagination
      ...ActivePinnedEnvironments
      ...ActiveStickyDeployments
    }
  }
`
export const Deployments: EntryPointComponent<
  {
    deploymentQuery: DeploymentsPageQuery
  },
  Record<string, never>
> = ({queries: {deploymentQuery}}) => {
  const [pinnedEnvConnectionId, setConnectionId] = useState('')
  const data = usePreloadedQuery<DeploymentsPageQuery>(CurrentDeployments, deploymentQuery)
  const repoScope = `${data.repository?.owner?.login}/${data.repository?.name}`
  const environment = useContext(EnvironmentContext)

  return (
    <Box
      data-hpc
      sx={{
        minHeight: '100%',
        '> div': {minHeight: '100%'},
        borderColor: 'border.default',
        borderBottomWidth: 0,
        borderTopWidth: 0,
        borderLeftWidth: 1,
        borderRightWidth: 0,
        borderStyle: 'solid',
      }}
    >
      <SplitPageLayout>
        <SplitPageLayout.Pane
          position="start"
          sticky
          width="large"
          divider={{regular: 'line', narrow: 'none'}}
          padding="condensed"
          sx={{
            '@media screen and (min-width: 769px)': {
              height: '100vh',
              maxHeight: '100vh !important',
            },
          }}
        >
          <Suspense fallback={<FallbackSpinner />}>
            <EnvironmentList
              adminable={data.repository?.viewerCanAdminister && data.repository?.canUseEnvironments}
              repositoryRef={data.repository as never}
              repoScope={repoScope}
              pinnedEnvConnectionId={pinnedEnvConnectionId}
            />
          </Suspense>
        </SplitPageLayout.Pane>
        <SplitPageLayout.Content width="xlarge" as="div">
          <Box sx={{maxWidth: '2015px'}}>
            <div>
              <div>
                <div>
                  <Suspense fallback={<FallbackSpinner />}>
                    {!environment && (
                      <ActiveDeployments
                        pinnedEnvConnectionId={pinnedEnvConnectionId}
                        setConnectionId={setConnectionId}
                        repositoryRef={data.repository as never}
                      />
                    )}
                  </Suspense>
                  <Suspense fallback={<FallbackSpinner />}>
                    <DeploymentsTable repositoryRef={data.repository as never} repoScope={repoScope} />
                  </Suspense>
                </div>
              </div>
            </div>
          </Box>
        </SplitPageLayout.Content>
      </SplitPageLayout>
    </Box>
  )
}
