/* eslint eslint-comments/no-use: off */
import {useRelayEnvironment} from 'react-relay'
import {useAppPayload} from '@github-ui/react-core/use-app-payload'
import type {AppPayload} from '../types/app-payload'
import {NavList, Button, Truncate} from '@primer/react'
import {useNavigate} from '@github-ui/use-navigate'
import {useContext, useState} from 'react'
import {EnvironmentContext} from '../App'
import type {EnvironmentListProps} from '../types/deployments'
import {PinIcon, PinSlashIcon} from '@primer/octicons-react'
import {pinEnvironment} from '../mutations'

type EnvironmentListItemProps = {
  id: string
  pinned: boolean
  name: string
  pinnedEnvConnectionId?: string
} & EnvironmentListProps

export const EnvironmentListItem = ({id, name, pinned, repoScope, pinnedEnvConnectionId}: EnvironmentListItemProps) => {
  const environment = useRelayEnvironment()
  const selectedEnv = useContext(EnvironmentContext)
  const {canManagePinning} = useAppPayload<AppPayload>()
  const navigate = useNavigate()
  const [pinIcon, setPinIcon] = useState('PinIcon')

  // Stop propagation will stop the click from "bubbling" the click to the
  // parent event (e.g. the NavList)
  const handlePinning: React.MouseEventHandler<HTMLButtonElement> = e => {
    e.preventDefault()
    e.stopPropagation()
    pinEnvironment({
      environment,
      envId: id,
      pinned: !pinned,
      connectionID: pinnedEnvConnectionId ? pinnedEnvConnectionId : '',
    })
  }

  const active = selectedEnv === name || (!selectedEnv && id === '0')

  return (
    <NavList.Item
      sx={{
        '& button': {
          display: 'none',
        },
        '&:hover button': {
          display: 'initial',
        },
      }}
      onClick={() => {
        if (name === 'All deployments' && id === '0') {
          navigate(`/${repoScope}/deployments`)
        } else {
          navigate(`/${repoScope}/deployments/${encodeURIComponent(name)}`)
        }
      }}
      aria-current={active ? 'page' : undefined}
      data-testid="deployments-environment"
    >
      <Truncate inline expandable title={name} sx={{maxWidth: 225, fontWeight: active ? 'bold' : 'normal'}}>
        {name}
      </Truncate>
      {id !== '0' && pinned && (
        <NavList.TrailingVisual>
          {canManagePinning ? (
            <Button
              onMouseOver={() => {
                setPinIcon('PinSlashIcon')
              }}
              onMouseOut={() => {
                setPinIcon('PinIcon')
              }}
              size="small"
              onClick={handlePinning}
              sx={{display: 'inline', mt: -1, mr: -2}}
              variant="invisible"
              trailingVisual={pinIcon === 'PinIcon' ? PinIcon : PinSlashIcon}
              aria-label={`Pin ${name} environment`}
              data-testid="unpin-environment"
            />
          ) : (
            <PinIcon />
          )}
        </NavList.TrailingVisual>
      )}

      {canManagePinning && id !== '0' && !pinned && (
        <NavList.TrailingVisual>
          <Button
            size="small"
            onClick={handlePinning}
            sx={{
              mt: -1,
              mr: -2,
              display: 'none',
              '.environment-list-item:hover &, .environment-list-item:focus &, .environment-list-item:focus-within &': {
                display: 'inline',
              },
            }}
            variant="invisible"
            trailingVisual={PinIcon}
            aria-label={`Pin ${name} environment`}
            data-testid="pin-environment"
          />
        </NavList.TrailingVisual>
      )}
    </NavList.Item>
  )
}

try{ EnvironmentListItem.displayName ||= 'EnvironmentListItem' } catch {}