import {Box, NavList, Heading} from '@primer/react'
import type {EnvironmentListProps} from '../types/deployments'
import {useContext, useMemo} from 'react'
import {EnvironmentContext} from '../App'
import type {FragmentRef} from 'react-relay'
import {graphql, usePaginationFragment, useFragment} from 'react-relay'
import type {EnvironmentListingsPinned$key} from './__generated__/EnvironmentListingsPinned.graphql'
import type {EnvironmentListListings$key} from './__generated__/EnvironmentListListings.graphql'
import type {EnvironmentListRefetch} from './__generated__/EnvironmentListRefetch.graphql'
import {EnvironmentListItem} from './EnvironmentListItem'
import {GearIcon} from '@primer/octicons-react'

interface EnvironmentSideProps extends EnvironmentListProps {
  repositoryRef: FragmentRef<EnvironmentListListings$key>
  adminable: boolean | undefined
  pinnedEnvConnectionId: string
}

type EnvironmentQueryProps = {
  key: EnvironmentListListings$key
}

type EnvironmentPinnedQueryProps = {
  key: EnvironmentListingsPinned$key
}

const RepositoryEnvironments = ({key}: EnvironmentQueryProps) => {
  const {data, loadNext, hasNext, isLoadingNext} = usePaginationFragment<
    EnvironmentListRefetch,
    EnvironmentListListings$key
  >(
    graphql`
      fragment EnvironmentListListings on Repository
      @argumentDefinitions(
        cursor: {type: "String"}
        first: {type: "Int", defaultValue: 50}
        pinnedEnvironmentFilter: {type: "EnvironmentPinnedFilterField", defaultValue: NONE}
      )
      @refetchable(queryName: "EnvironmentListRefetch") {
        environments(first: $first, after: $cursor, pinnedEnvironmentFilter: $pinnedEnvironmentFilter)
          @connection(key: "Repository_environments") {
          edges {
            node {
              id
              name
              isPinned
            }
          }
        }
      }
    `,
    key,
  )
  return {data, loadNext, hasNext, isLoadingNext}
}

const PinnedEnvironments = ({key}: EnvironmentPinnedQueryProps) => {
  return useFragment(
    graphql`
      fragment EnvironmentListingsPinned on Repository
      @argumentDefinitions(
        cursor: {type: "String"}
        first: {type: "Int", defaultValue: 10}
        pinnedEnvironmentFilter: {type: "EnvironmentPinnedFilterField", defaultValue: ONLY}
      ) {
        listPinnedEnvironments: environments(
          first: $first
          after: $cursor
          pinnedEnvironmentFilter: $pinnedEnvironmentFilter
        ) @connection(key: "Repository_listPinnedEnvironments") {
          edges {
            node {
              id
              name
              isPinned
            }
          }
        }
      }
    `,
    key,
  )
}

export function EnvironmentList({adminable, repositoryRef, repoScope, pinnedEnvConnectionId}: EnvironmentSideProps) {
  const {data: envData, loadNext: loadNextEnvs, hasNext: hasNextEnvs} = RepositoryEnvironments({key: repositoryRef})
  const pinnedEnvData = PinnedEnvironments({key: repositoryRef})
  const selectedEnv = useContext(EnvironmentContext)
  const environmentsList = useMemo(() => {
    const unpinnedEnvs =
      envData.environments?.edges?.map(environment => {
        return {
          name: environment?.node?.name,
          id: environment?.node?.id,
          pinned: environment?.node?.isPinned,
        }
      }) || []
    const pinnedEnvs =
      pinnedEnvData.listPinnedEnvironments?.edges?.map(environment => {
        return {
          name: environment?.node?.name,
          id: environment?.node?.id,
          pinned: environment?.node?.isPinned,
        }
      }) || []
    return [...pinnedEnvs, ...unpinnedEnvs]
  }, [envData, pinnedEnvData])
  const envNotInList = useMemo(
    () => selectedEnv && !environmentsList?.some(env => env.name === selectedEnv),
    // Keep `environmentsList` out of the dependencies list so that fetching more pages of environments
    // doesn't recalculate this value.
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [selectedEnv],
  )

  return (
    <div>
      <Box sx={{display: 'flex', alignItems: 'center', flexWrap: 'wrap', px: 2, pt: 1}}>
        <Heading
          as="h1"
          sx={{
            fontSize: 3,
          }}
        >
          Deployments
        </Heading>
      </Box>
      <NavList data-testid="environment-list">
        <EnvironmentListItem id={'0'} pinned={false} name={'All deployments'} repoScope={repoScope} />
        <NavList.Group title="Environments">
          {envNotInList && (
            <EnvironmentListItem
              id={'-1'}
              name={selectedEnv ? selectedEnv : ''}
              pinned={false}
              repoScope={repoScope}
              pinnedEnvConnectionId={pinnedEnvConnectionId}
            />
          )}
          {environmentsList &&
            environmentsList.map(
              (environment, index) =>
                environment.pinned && (
                  <EnvironmentListItem
                    id={environment.id!}
                    key={index}
                    pinned={environment.pinned}
                    name={environment.name!}
                    repoScope={repoScope}
                    data-testid="pinned-environment"
                    pinnedEnvConnectionId={pinnedEnvConnectionId}
                  />
                ),
            )}

          {environmentsList &&
            environmentsList.map((environment, index) =>
              (envNotInList && environment.name === selectedEnv) || environment.pinned! ? null : (
                <EnvironmentListItem
                  id={environment.id!}
                  key={index}
                  pinned={environment.pinned!}
                  name={environment.name!}
                  repoScope={repoScope}
                  pinnedEnvConnectionId={pinnedEnvConnectionId}
                />
              ),
            )}
          {hasNextEnvs && (
            <NavList.Item className="color-fg-accent" onClick={() => loadNextEnvs(50)}>
              Show more environments
            </NavList.Item>
          )}
          <NavList.Divider />
          {adminable && (
            <NavList.Item href={`/${repoScope}/settings/environments`}>
              <NavList.LeadingVisual>
                <GearIcon />
              </NavList.LeadingVisual>
              Manage environments
            </NavList.Item>
          )}
        </NavList.Group>
      </NavList>
    </div>
  )
}

try{ RepositoryEnvironments.displayName ||= 'RepositoryEnvironments' } catch {}
try{ PinnedEnvironments.displayName ||= 'PinnedEnvironments' } catch {}
try{ EnvironmentList.displayName ||= 'EnvironmentList' } catch {}