import type React from 'react'
import {RelayEnvironmentProvider} from 'react-relay/hooks'
import {relayEnvironmentWithMissingFieldHandlerForNode} from '@github-ui/relay-environment'
import {PreviewCardOutlet} from '@github-ui/preview-card'
import type {Environment} from 'react-relay'
import {createContext} from 'react'
import {ssrSafeLocation} from '@github-ui/ssr-utils'

export const EnvironmentContext = createContext<string | null>(null)
const DEPLOYMENT_REGEX = new RegExp('/[^/]+/[^/]+/deployments/(\\S+)')

export function App({children, environment}: {children?: React.ReactNode; environment?: Environment}) {
  const match = ssrSafeLocation.pathname.match(DEPLOYMENT_REGEX)

  let initialEnv: string | null = (match && match[1]) || null
  if (initialEnv) {
    initialEnv = decodeURIComponent(initialEnv)
  }

  return (
    <EnvironmentContext.Provider value={initialEnv}>
      <RelayEnvironmentProvider environment={environment ?? relayEnvironmentWithMissingFieldHandlerForNode()}>
        {children}
      </RelayEnvironmentProvider>
      <PreviewCardOutlet />
    </EnvironmentContext.Provider>
  )
}

try{ EnvironmentContext.displayName ||= 'EnvironmentContext' } catch {}
try{ DEPLOYMENT_REGEX.displayName ||= 'DEPLOYMENT_REGEX' } catch {}
try{ App.displayName ||= 'App' } catch {}