import {Box, BranchName, Text, Link, Truncate} from '@primer/react'
import {branchPath, tagPath} from '@github-ui/paths'
import {testIdProps} from '@github-ui/test-id-props'
import type {DeploymentData} from '../../types/list'

export const GitRef = ({
  owner,
  repo,
  gitRef,
  refExists,
  pullRequestID,
  pullRequestPath,
  prefix,
}: Pick<
  DeploymentData,
  'owner' | 'repo' | 'gitRef' | 'refExists' | 'sha' | 'pullRequestID' | 'pullRequestPath' | 'prefix'
>) => {
  let refLink
  if (gitRef) {
    if (refExists) {
      const linkHref =
        prefix === 'refs/heads/' ? branchPath({owner, repo, branch: gitRef}) : tagPath({owner, repo, tag: gitRef})
      const linkLabel = prefix === 'refs/heads/' ? `${gitRef} branch` : `${gitRef} tag`
      refLink = (
        <BranchName href={linkHref} aria-label={linkLabel} {...testIdProps('deployments-list-branch-name')}>
          <Truncate title={gitRef}>{gitRef}</Truncate>
        </BranchName>
      )
    } else {
      refLink = (
        <BranchName
          as="code"
          sx={{
            color: 'fg.default',
            bg: 'neutral.muted',
          }}
          {...testIdProps('deployments-list-branch-name')}
        >
          <Truncate title={gitRef}>{gitRef}</Truncate>
        </BranchName>
      )
    }
  }

  return (
    <Box as="span" sx={{display: 'flex', alignItems: 'right'}}>
      {refLink}
      <span>
        {pullRequestID && pullRequestPath && (
          <Link href={pullRequestPath} data-hovercard-url={`${pullRequestPath}/hovercard`}>
            <Text as="span" sx={{ml: 1, fontSize: 0}}>
              (#{pullRequestID})
            </Text>
          </Link>
        )}
      </span>
    </Box>
  )
}

try{ GitRef.displayName ||= 'GitRef' } catch {}