/* eslint eslint-comments/no-use: off */
import {Box, Text, Label} from '@primer/react'
import type {DeploymentData} from '../types/list'
import {STATUSES} from '../utils/statuses'
import {commitPath} from '@github-ui/paths'
import {getInProgressSpinner} from './Spinner'
import {Trigger} from './list-components/Trigger'
import {ListItemDescription} from '@github-ui/list-view/ListItemDescription'
import {ListItemLeadingContent} from '@github-ui/list-view/ListItemLeadingContent'
import {ListItemLeadingVisual} from '@github-ui/list-view/ListItemLeadingVisual'
import {ListItemMainContent} from '@github-ui/list-view/ListItemMainContent'
import {ListItemTitle} from '@github-ui/list-view/ListItemTitle'
import {ListItem} from '@github-ui/list-view/ListItem'
import {DeploymentListItemSecondaryActions} from './DeploymentListItemSecondaryActions'
import {DeploymentListItemMetadata} from './DeploymentListItemMetadata'
import {graphql, useLazyLoadQuery} from 'react-relay'
import type {DeploymentListItemQuery} from './__generated__/DeploymentListItemQuery.graphql'
import type {SafeHTMLString} from '@github-ui/safe-html'

export type Deployment = Omit<
  DeploymentData,
  'environment' | 'createdAt' | 'refExists' | 'prefix' | 'pullRequestID' | 'pullRequestPath'
> & {
  environment?: DeploymentData['environment']
  createdAt?: DeploymentData['createdAt']
}

type DeploymentListItemProps = {latestId: number} & Deployment

const KebabActionMenu = (logURL: string | undefined, workflowFilePath: string | undefined) => {
  if (logURL || workflowFilePath) {
    return <DeploymentListItemSecondaryActions logURL={logURL} workflowFilePath={workflowFilePath} />
  }
}

export const DeploymentCommitGraphQLQuery = graphql`
  query DeploymentListItemQuery($deploymentNodeId: ID!) {
    node(id: $deploymentNodeId) {
      ... on Deployment {
        ref {
          name
          prefix
        }
        commit {
          associatedPullRequests(last: 1) {
            nodes {
              number
              permalink
            }
          }
        }
      }
    }
  }
`

export const DeploymentListItem = ({
  id,
  node,
  latestId,
  name,
  owner,
  repo,
  gitRef,
  sha,
  environmentURL,
  logURL,
  workflowFilePath,
  state,
  environment,
  creatorName,
  workflowRunNumber,
  workflowName,
  creatorPath,
  createdAt,
  creatorAvatar,
}: DeploymentListItemProps) => {
  const DeploymentCommit = useLazyLoadQuery<DeploymentListItemQuery>(DeploymentCommitGraphQLQuery, {
    deploymentNodeId: node,
  })

  const associatedPullRequest = DeploymentCommit.node?.commit?.associatedPullRequests?.nodes?.at(0)

  return (
    <ListItem
      title={
        <ListItemTitle
          value={name as SafeHTMLString}
          href={commitPath({owner, repo, commitish: sha})}
          headingSx={{
            color: 'fg.default',
            textDecoration: 'none',
            fontWeight: 600,
            fontSize: 1,
            maxWidth: '100%',
            whiteSpace: 'nowrap',
          }}
          containerSx={{display: 'flex', alignItems: 'flex-start', gap: 1, maxWidth: '95%'}}
        >
          {id === latestId && <Label variant="secondary">Active</Label>}
        </ListItemTitle>
      }
      metadata={
        <DeploymentListItemMetadata
          pullRequestID={associatedPullRequest?.number}
          pullRequestPath={associatedPullRequest?.permalink}
          gitRef={gitRef}
          refExists={DeploymentCommit.node?.ref != null}
          environmentURL={environmentURL}
          prefix={DeploymentCommit.node?.ref?.prefix ?? ''}
          createdAt={createdAt}
          owner={owner}
          repo={repo}
          sha={sha}
        />
      }
      secondaryActions={KebabActionMenu(logURL, workflowFilePath)}
    >
      <ListItemLeadingContent>
        {state === undefined ? (
          <ListItemLeadingVisual
            aria-label={STATUSES['queued'].label}
            icon={STATUSES['queued'].icon}
            color={STATUSES['queued'].color}
            description={STATUSES['queued'].description}
          />
        ) : state === 'in_progress' ? (
          getInProgressSpinner()
        ) : (
          <ListItemLeadingVisual
            aria-label={STATUSES[state].label}
            icon={STATUSES[state].icon}
            color={STATUSES[state].color}
            description={STATUSES[state].description}
          />
        )}
      </ListItemLeadingContent>
      <ListItemMainContent>
        <ListItemDescription>
          <Box sx={{display: 'flex', flexWrap: 'wrap'}}>
            <Text flex-wrap="nowrap" sx={{mr: 1}}>
              {state === 'in_progress' ? 'Deploying to' : STATUSES[state]?.message || 'Queued to deploy to'}
            </Text>
            <Text className="text-bold" sx={{mr: 1, flexWrap: 'nowrap'}}>
              {environment}
            </Text>
            <Text sx={{mr: 1, flexWrap: 'nowrap'}}>by</Text>
            <Box sx={{flexWrap: 'nowrap'}}>
              <Trigger
                creatorName={creatorName}
                workflowRunNumber={workflowRunNumber}
                workflowName={workflowName}
                creatorPath={creatorPath}
                creatorAvatar={creatorAvatar}
              />
            </Box>
          </Box>
        </ListItemDescription>
      </ListItemMainContent>
    </ListItem>
  )
}

try{ KebabActionMenu.displayName ||= 'KebabActionMenu' } catch {}
try{ DeploymentListItem.displayName ||= 'DeploymentListItem' } catch {}
try{ DeploymentCommit.displayName ||= 'DeploymentCommit' } catch {}