import {type SuppliedFilterProviderOptions, type FilterKey, FilterProviderType} from '@github-ui/filter'
import type {UserFilterParams} from '@github-ui/filter/providers'
import {PersonIcon} from '@primer/octicons-react'
import {BaseUserFilterProvider} from '@github-ui/filter/providers'

export const CREATOR_FILTER_KEY: FilterKey = {
  displayName: 'Creator',
  key: 'creator',
  priority: 2,
  icon: PersonIcon,
  description: 'Filter by deployment creator',
}

export type Creator = {
  login: string
}

export class CreatorFilterProvider extends BaseUserFilterProvider {
  constructor(filterParams: UserFilterParams, options?: SuppliedFilterProviderOptions) {
    const CREATOR_DEPLOYMENT_ENDPOINT = `/${filterParams.repositoryScope}/deployments/filter-suggestions/users`
    super(filterParams, CREATOR_FILTER_KEY, options)
    this.type = FilterProviderType.Select
    this.suggestionEndpoint = CREATOR_DEPLOYMENT_ENDPOINT
    this.validationEndpoint = CREATOR_DEPLOYMENT_ENDPOINT
  }
}
