/* eslint eslint-comments/no-use: off */
import {ActionList} from '@primer/react'
import {ListItemActionBar} from '@github-ui/list-view/ListItemActionBar'
import type {DeploymentData} from '../types/list'

type DeploymentListItemSecondaryActionsProps = Pick<DeploymentData, 'logURL' | 'workflowFilePath'>

export const DeploymentListItemSecondaryActions = ({
  logURL,
  workflowFilePath,
}: DeploymentListItemSecondaryActionsProps) => (
  <ListItemActionBar
    staticMenuActions={[
      {
        key: 'view-logs',
        render: () => {
          if (!logURL) return null
          return <ActionList.LinkItem href={logURL}>View logs</ActionList.LinkItem>
        },
      },
      {
        key: 'view-workflow',
        render: () => {
          if (!workflowFilePath) return null
          return <ActionList.LinkItem href={workflowFilePath}>View workflow run</ActionList.LinkItem>
        },
      },
    ]}
  />
)

try{ DeploymentListItemSecondaryActions.displayName ||= 'DeploymentListItemSecondaryActions' } catch {}